let fontUnit = 'upx'

fontUnit = 'rpx'

export default {
	name: 'BjxInputs',
	props: {
		placeholder: {
			type: String,
			default: ''
		},
		value: {
			type: String,
			default: ''
		},
		itemKey: {
			type: String,
			default: ''
		},
		width: {
			type: String,
			default: '600'
		},
		disabled: {
			type: Boolean,
			default: false
		},
		timeDelay: {
			type: Number,
			default: 20
		},
		theStyle: {
			type: String,
			default: ''
		},
		overflow: {
			type: String,
			default: 'auto'
		},
	},
	data() {
		return {
			timer: null,
			data: [],
			isShow: false,
			theValue: this.value,
			theDisabled: this.disabled,
		}
	},
	watch: {
		value(val){
			this.theValue = val
		}
	},
	computed: {
		show(){
			return this.isShow && this.data.length
		},
		getStyle(){
			let style = this.theStyle.replace(/upx/g,fontUnit)
			let width = 'width:' + (!isNaN(Number(this.width)) ? this.width + fontUnit : this.width) + ';'
			return style + width
		},
	},
	methods: {
		showItem(){
			this.isShow = this.data.length > 0
		},
		theFocus(e){
			this.$emit('focus',e)
		},
		theBlur(e){
			this.$emit('blur',e)
		},
		theInput(e) {
			clearTimeout(this.timer)
			this.timer = setTimeout(()=>{
				this.$emit('list',{value: e.detail.value,callback: this.setData})
			},this.timeDelay)
		},
		getItem(item) {
			// console.log(item[this.itemKey])
			// console.log(this.itemKey && typeof item == 'object' )
			 // console.log(this.itemKey)
			return this.itemKey && typeof item == 'object' ? item[this.itemKey] : item
		},
		selectItem(item,index) {
			// console.log(index)
			this.isShow = false
			this.theValue = this.itemKey ? item[this.itemKey] : item
			this.$emit('select',item,index)
		},
		setData(data) {
			if(data) {
				this.isShow = true
				this.data = data
			}
		},
		setValue(value){
			this.theValue = value
		},
		setDisabled(value){
			this.theDisabled = value
		},
	},
}
